@use '../../assets/scss/elements/scroll';

:host {
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;

  .ath-menu-button-container {
    position: relative;

    .ath-menu-button-disabled {
      pointer-events: none;
    }

    .ath-menu-button-overlay {
      position: absolute;
      display: inline-flex;
      padding: var(--ath-spacing-menu-button-overlay-padding);
      padding-right: calc(var(--ath-spacing-menu-button-overlay-padding) / 2);
      flex-direction: column;
      align-items: flex-start;
      gap: var(--ath-spacing-menu-button-overlay-row-gap);
      border-radius: var(--ath-border-radius-3xl);
      border: var(--ath-border-width-xs) solid var(--ath-color-border-alpha-bolder-default);
      background: var(--ath-color-menu-button-overlay-bg);
      margin-top: 4px;
      z-index: 1000;
      &-wrapper {
        padding-right: calc(var(--ath-spacing-menu-button-overlay-padding) / 2);
      }
    }

    .position {
      &--right {
        right: 0px;
      }
      &--left {
        left: 0px;
      }
    }
  }
}
